<template>
  <v-card id="user-list">
    <!-- create -->
    <v-card-text>
      <div class="d-flex pb-2 ma-1">
        <v-btn
          color="primary"
          class="me-3"
          :to="{ name: 'apps-user-add' }"
        >
          <v-icon
            size="18"
            class="me-1"
          >
            {{ mdiPlus }}
          </v-icon>
          <span>Create user</span>
        </v-btn>
        <br>

        <v-text-field
          v-model='search'
          single-line
          dense
          outlined
          hide-details
          placeholder="Search user"
          class="user-list-search me-3 align-end"
        ></v-text-field>
        </div>
        </v-card-text>
    <!-- table -->

<template>
  <v-simple-table height="250">
    <template v-slot:default>
      <thead>
        <tr>
          <th class="text-uppercase">
            ID
          </th>
          <th class="text-center text-uppercase">
           Name
          </th>
          <th class="text-center text-uppercase">
           Email
          </th>
          <th class="text-center text-uppercase">
            Role
          </th>

          <th class="text-center text-uppercase">
            Actions
          </th>
        </tr>
      </thead>

      <tbody v-if="user.length">
        <tr
          v-for="item in search_result"
          :key="item.id"
        >

          <td>{{ item.id }}</td>
          <td>
          <!-- <div class="d-flex flex-column ms-4">
            <span class="d-block text--primary  font-weight-semibold text-truncate">{{ item.customer.first_name }} {{ item.customer.last_name }}</span>
            <span class="text-xs">{{ item.customer.email }}</span> -->
          <!-- </div> -->
          </td>
          <td class="text-center">
            {{ item.email }}
          </td>
          <td class="text-center">
            {{ item.role }}
          </td>
          <td class ="text-center">
            <button @click="edituser(item.id)">
              <v-icon
                size="18"
                class="me-1"
              >
              {{ mdiPencilOutline }}
              </v-icon>
            </button>
            <button @click="deleteUser(item.id)">
              <v-icon
                size="18"
                class="me-1"
              >
              {{ mdiDeleteOutline}}
              </v-icon>
            </button>
          </td>

        </tr>
      </tbody>
      <tbody v-else>
        <tr>
          <td colspan="7" class="text-center">
            No user Available
          </td>
        </tr>
      </tbody>
    </template>
  </v-simple-table>
  </template>
  </v-card>
</template>

<script>

import {
  mdiPlus,
  mdiAccountCircleOutline,
  mdiDeleteOutline,
  mdiPencilOutline,
} from '@mdi/js';

import { avatarText } from '@core/utils/filter';
import store from '@/store';
import axios from "@axios";

export default{
  data() {
    return{
    user:[],
    search:""

    }
  },
  mounted() {
    this.getUser();
  },
  methods:{

    getUser(){
      axios
        .get('/auth/users/')
        .then(response => {
        this.user = response.data;

      })
      .catch(error=>{
        console.log(error.response)
      });

    },
    deleteuser(id){
      axios
        .delete('/user/'+id)
        .then(
        this.$router.push('/apps/user/list')
        )
      .catch(error =>{
        console.log(error.response.data);
      });

    },
    edituser(id){
      this.$router.push({ name: 'apps-user-edit', params: { id: id } });
    },
  },
  computed:{
    search_result(){
      if(this.search){
        return this.user.filter(item => {
          return item.email.toLowerCase().includes(this.search.toLowerCase());
        });
      }
      else{
        return this.user
      }
    }


  },
  setup(){
    return{
      mdiPlus,
      mdiPencilOutline,
      mdiDeleteOutline,
      avatarText,
      store,
    }
  }
}


</script>

<style lang="scss" scoped>
#quota-list {
  .quota-list-search {
    max-width: 10.625rem;
  }
}
</style>
